footer{display:-webkit-flex;display:-ms-flexbox;display:flex;min-height:260px;margin-left:280px;padding:30px 50px;background-image:-webkit-gradient(linear, left top, right bottom, color-stop(50%, #1130ac), color-stop(50%, #0b2072));background-image:-webkit-linear-gradient(left top, #1130ac 50%, #0b2072 50%);background-image:linear-gradient(to right bottom, #1130ac 50%, #0b2072 50%)}footer .group{-webkit-flex:1 1;-ms-flex:1 1;flex:1 1;margin-bottom:40px}footer .group h2{font-weight:500;font-size:16px;color:#fff;margin-top:0;margin-bottom:20px}footer .group ul{list-style:none;margin:0;padding:0}footer .group ul li{margin:12px 0;line-height:1;color:rgba(255,255,255,.4)}footer .group a{font-size:14px;color:#fff}footer .group a:hover{text-decoration:underline}